import Axios from "axios";

export const NATIVECURRENCY_CHANGE = "NATIVECURRENCY_CHANGE";
export const NATIVECURRENCY_UPDATE_PRICE = "NATIVECURRENCY_UPDATE_PRICE";

export const changeNativeCurrency = ticker => {
  return {
    type: NATIVECURRENCY_CHANGE,
    ticker: ticker
  };
};

export const getNativeCurrencyPrice = ticker => {
  return dispatch => {
    Axios.get("https://api.exchangerate-api.com/v4/latest/USD").then(
      response => {
        let price = response.data.rates[ticker];
        console.log(price);
        dispatch(updateNativeCurrencyPrice(price));
      }
    );
  };
};

export const updateNativeCurrencyPrice = price => {
  return {
    type: NATIVECURRENCY_UPDATE_PRICE,
    price: price
  };
};

export const getNativeCurrencyPriceFailed = () => {};
